body{
margin: 0;
}

.container{
    display: flex;
    flex-direction: column;
    max-width: 1340px;
    margin:50px auto;
    margin-top: 0;
    padding: 0 50px;
}
@media screen and (max-width: 900px) {
    
    .container{
        padding: 0;
        margin: 0 20px;
        min-height: 73vh;
    }
}